:root{
    --content : space-between;
}

.card{
    padding: 2px 0;
}
.cardItem{
    margin-top: 5px;
    width: 100%;
    display: flex;
    background-color: #fff;
    flex-direction: column;
    padding: 5px;
    border-radius: 3px;
    cursor: grab;
    box-shadow: 0 1px 0 rgba(9,30,66,.25)
}

.cardItem:first-child{
    margin: 0;
}

.cardItem .cardItem__ticket{
    max-width: 260px;
    display: grid;
    grid-template-columns: repeat(5,1fr);
    grid-column-gap: 2px;
    grid-row-gap: 2px;
}

.cardItem .cardItem__text{
    display: flex;
    flex-direction: row;
    justify-content: var(--content);
}

.cardItem .cardItem__footer{
    display: flex;
    flex-direction: row;
    justify-content: var(--content);
}